@font-face {
  font-family: "Rubik";
  /*src:  url('assets/fonts/Rubik-VariableFont_wght.ttf') format('truetype')*/
  src: url("../assets/fonts/Rubik-Bold.ttf") format("truetype");
}
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: "Rubik";
  background: rgb(54 57 66);
  background: linear-gradient(
    0deg,
    rgb(54 57 66 / 100%) 0%,
    rgb(29 31 33 / 100%) 50%,
    rgb(11 10 8 / 100%) 100%
  );
  position: relative;
}

#game-root {
  position: absolute;
  overflow: hidden;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

#game-canvas {
  position: absolute;
  overflow: hidden;
  margin: 0;
  padding: 0;
  user-select: none;
}
